import { AppPlayerSection } from 'AddPlayerSection'
import { ListedPlayer } from 'ListedPlayer'
import { useState } from 'react'
import { shuffle } from 'shuffle'
import { allteamNames } from 'TeamNames'
import './App.scss'

export interface Player {
    name: string
    rank: number
}

interface AppState {
    players: Player[]
    storedPlayers: Player[]
    playerInput: string
    teamOne: string[]
    teamTwo: string[]
    quickAddPlayers: Player[]
    teamOneName: string
    teamTwoName: string
}

const initialState: AppState = {
    players: [],
    storedPlayers: [
        {name: "Sara", rank: 0},
        {name: "Austin", rank: 0},
        {name: "Chase", rank: 0},
        {name: "Hunter", rank: 0},
        {name: "Michael", rank: 0},
        {name: "Josh", rank: 0},
        {name: "James", rank: 0},
        {name: "Stout", rank: 0},
        {name: "Reuben", rank: 0},
        {name: "Katie", rank: 0},
    ],
    playerInput: "",
    teamOne: [],
    teamTwo: [],
    quickAddPlayers: [
        {name: "Sara", rank: 0},
        {name: "Austin", rank: 0},
        {name: "Chase", rank: 0},
        {name: "Hunter", rank: 0},
        {name: "Michael", rank: 0},
        {name: "Josh", rank: 0},
        {name: "James", rank: 0},
        {name: "Stout", rank: 0},
        {name: "Reuben", rank: 0},
        {name: "Katie", rank: 0},
    ],
    teamOneName: "",
    teamTwoName: ""
}

export const App = () => {

    const startingMMR = 0
    const [state, setState] = useState(initialState)

    const addPlayerToShuffle = (name: string) => {
        if (name !== null && name != "") {
            console.log(state.players)
            if (state.players.map(p => p.name).includes(name)) {
                console.log("A player with this name already exists.")
                //Present Modal
            } else {
                const mmr = playerIsStored(name) ? getPlayerMMR(name) : startingMMR
                const newArray = [...state.players, {name: name, rank: startingMMR}]
                setState( s => ({...s, players: newArray, playerInput: ""}))
            } 
        }  
    }

    const playerIsStored = (name: string) => {
        return state.storedPlayers.map(p => p.name).includes(name)
    }

    const getPlayerMMR = (name: string) => {

    }

    const playerTextChanged = (text: string) => {
        setState( s => ({...s, playerInput: text}))
    }
    
    const shuffleButtonWasPressed = () => {
        const shuffledArray = shuffle(state.players.map(p => p.name))

        const midIndex = Math.floor(shuffledArray.length / 2)

        const firstHalf = shuffledArray.slice(0, midIndex)
        const secondHalf = shuffledArray.slice(midIndex, shuffledArray.length)

        const teamOneName = randomlyGeneratedTeamName("")
        const teamTwoName = randomlyGeneratedTeamName(teamOneName)

        setState( s => ({...s, teamOneName: teamOneName, teamTwoName: teamTwoName, teamOne: firstHalf, teamTwo: secondHalf}))
    }

    const randomlyGeneratedTeamName = (otherTeamName: string) => {
        const allTeamNames = allteamNames.filter(n => n != otherTeamName)
        const randomName = shuffle(allTeamNames)[0]
        return randomName
    }

    const playerDeleteButtonWasPressed = (playerIndex: number) => {
        const newPlayers = state.players.filter( (p, i) => (i !== playerIndex))
        setState( s => ({...state, players: newPlayers}))
    }

    const copyButtonWasPressed = () => {
        const textToCopy = ` TEAM ONE (${state.teamOneName})\n ----------\n${state.teamOne.map(p =>` ${p}`)}\n\n TEAM TWO (${state.teamTwoName})\n ----------\n${state.teamTwo.map(p =>` ${p}`)}`
        
        navigator.clipboard.writeText(textToCopy).then(function() {
            //INDICATE TEXT COPIED
          }, function() {
            //CLIPBOARD NOT ALLOWED MESSAGE
          });
    }

    const clearAllButtonWasPressed = () => {
        setState( s => ({...state, players: [], teamOne: [], teamTwo: []}))
    }

    return (
        <div>
            <div id="side-bar">
            <h1>QUICK ADD</h1>
            <div id="quick-add-section">
            {state.quickAddPlayers.sort().map(p => <button className="quick-add-button" onClick={() => addPlayerToShuffle(p.name)}>{p.name}</button>)}
            </div>
            <hr></hr>
            <AppPlayerSection onTextInputChangedFunction={playerTextChanged} textInput={state.playerInput} addPlayerButtonFunction={addPlayerToShuffle}></AppPlayerSection>
            <hr></hr>
            {state.players.map((p, i) => (<ListedPlayer playerDeletedButtonWasPressedFunction={playerDeleteButtonWasPressed} name={p.name} index={i}></ListedPlayer>))}
            </div>
            <div id="mid-page-section">
            <button className="shuffle-button" onClick={() => shuffleButtonWasPressed()}>Shuffle</button>
            <div></div>
            <button className="action-button" onClick={() => copyButtonWasPressed()}>COPY</button>
            <button className="action-button" onClick={() => clearAllButtonWasPressed()}>CLEAR ALL</button>
            <h1>{`TEAM ONE (${state.teamOneName})`}</h1>
            {state.teamOne.map(p => <p>{p}</p>)}
            <h1>{`TEAM TWO (${state.teamTwoName})`}</h1>
            {state.teamTwo.map(p => <p>{p}</p>)}
            </div>
        </div>
    )
}